:root {
  --fontColor: #222;
  --backgroundColor: #fff;
  --accentColor: #c4131c;
  --borderColor: #dfdfdf;
  --boxShadow: 0 1px 3px 0 rgba(0, 0, 0, 0.05);

  --versionTextColor: #666;
  --versionBorderColor: #a0a0a0;

  --horizontalContentPadding: 1.25rem;
}

@media (prefers-color-scheme: dark) {
  :root {
    --fontColor: #fff;
    --borderColor: #404040;
    --backgroundColor: #222;
    --accentColor: #e25245;
    --boxShadow: 0 1px 5px 0 rgba(0, 0, 0, 1);

    --versionTextColor: #ccc;
    --versionBorderColor: #999;
  }
}

*,
*:before,
*:after {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  width: 100%;
  max-width: 630px;
  margin: 0 auto;
  color: #222;
  color: var(--fontColor);
}

html,
body {
  height: 100%;
  background-color: #fff;
  background-color: var(--backgroundColor);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#root {
  min-height: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}

/* This sets the cursor to be a more reasonable default */

body {
  cursor: default;
}

code {
  cursor: text;
}

a,
button,
input[type='button'],
input[type='radio'],
input[type='submit'],
input[type='checkbox'] {
  cursor: pointer;
}

button[disabled],
input[disabled] {
  cursor: default;
}

.app {
  -webkit-flex: 1 1;
          flex: 1 1;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}

.blogHeader {
  margin: 2.625rem 20px;
  display: -webkit-flex;
  display: flex;
}

.blogHeader_text {
  font-size: 2.75rem;
  font-weight: 900;
  margin: 0;
  padding: 0;
}

.blogHeader_link {
  color: var(--fontColor);
  text-decoration: none;
}

.blogHeader_logo {
  width: 50px;
  margin-right: 1rem;
}

.blogDescription {
  margin: 0 0 2.625rem;
  padding: 0 20px;
  line-height: 1.75;
}

.postsList {
  -webkit-flex: 1 1;
          flex: 1 1;
}

.postContent {
  border-bottom: 1px solid var(--borderColor);
  border-right: 0;
  border-left: 0;
  padding: 2.5rem 20px;
}

.postContent:first-child {
  border-top: 1px solid var(--borderColor);
}

.postContent:last-child {
  margin-bottom: 0;
}

@media (min-width: 650px) {
  .postContent {
    padding: 25px;
    border: 1px solid var(--borderColor);
    border-radius: 8px;
    margin: 0 20px 3rem;
    box-shadow: var(--boxShadow);
  }
}

.postContent_header {
  margin: 0;
  padding: 0;
  font-size: 1.125rem;
  font-weight: 700;
}

.postContent-standalone {
  padding-top: 0;
  margin-bottom: 2.5rem;
}

@media (min-width: 650px) {
  .postContent-standalone {
    padding: 25px;
    margin-bottom: 3rem;
  }
}

.postContent-standalone .postContent_header {
  font-size: 1.5rem;
}

.postContent_headerLink {
  color: var(--textColor);
  text-decoration: none;
}

.postContent_text {
  line-height: 1.75;
}

.postContent_media {
  width: 100%;
  display: block;
}

.postContent_video {
  cursor: pointer;
}

.postContent_mediaContainer {
  position: relative;
  margin-bottom: 1rem;
  border-radius: 6px;
  overflow: hidden;
}

.postContent_videoPlayBtn {
  margin-bottom: 10px;
}

.postContent_publishDate {
  margin-top: 20px;
  padding-top: 20px;
  font-family: Georgia, 'Times New Roman', Times, serif;
  font-size: 0.875rem;
  position: relative;
}

.postContent_publishDate:after {
  content: '';
  position: absolute;
  top: 0;
  border-top: 1px solid #ddd;
  width: 35px;
  left: 0;
}

.postContent_fixedStatus {
  border-radius: 4px;
  border: 1px solid #109c44;
  display: inline-block;
  color: #109c44;
  padding: 3px 5px;
  font-size: 0.8125rem;
  font-weight: 600;
  margin-left: 10px;
}

.postContent-standalone .postContent_fixedStatus {
  font-size: 0.875rem;
  padding: 5px 8px;
}

.postContent_softwareVersion {
  font-size: 0.8125rem;
  border: 1px solid var(--versionBorderColor);
  color: var(--versionTextColor);
  font-weight: 500;
  border-radius: 5px;
  padding: 5px 6px;
  display: inline-block;
  margin-right: 7px;
}

.pagination {
  padding: 0 20px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: 1.1rem;
  margin-top: 2.625rem;
}

.paginationLink {
  color: var(--accentColor);
  font-weight: 500;
  margin-right: 10px;
  font-weight: 500;
}

.paginationLink-active {
  color: var(--textColor);
  margin-right: 10px;
  font-weight: 500;
}

.paginationLink-active:last-child,
.paginationLink:last-child {
  margin-right: 0;
}

footer {
  margin: 2.625rem 0;
  padding: 0 20px;
}

footer a {
  font-weight: 500;
  color: var(--accentColor);
  font-size: 1rem;
  position: relative;
  text-decoration: none;
}

.post_returnLink {
  padding: 0 20px;
  text-decoration: none;
  color: var(--accentColor);
  display: -webkit-flex;
  display: flex;
  font-weight: 500;
  -webkit-align-items: center;
          align-items: center;
  margin-bottom: 2rem;
}

.post_returnIcon {
  margin-right: 5px;
}

.postHeader {
  font-size: 1.9rem;
  font-weight: 800;
  margin-bottom: 2.7rem;
  padding: 0 20px;
  display: -webkit-flex;
  display: flex;
}

.postHeader_logo {
  width: 32px;
  margin-right: 0.875rem;
  display: block;
}

.postHeader_link {
  color: var(--fontColor);
  text-decoration: none;
}

